* {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  height: 100vh;
  text-align: left;
  color: #000000;
  letter-spacing: 0.2em;
  font-weight: 700;
  font-family: "Roboto";
  overflow: hidden;
  background-image: url(./images/osaka.jpg);
  background-size: cover;
  background-attachment: fixed;
  opacity: 0.9;
}

header span {
  font-size: 56px;
}

header p {
  padding-top: 55vh;
  padding-left: 4vw;
  font-size: 24px;
  max-width: 550px;
  opacity: 1;
  animation: wordDisplay 2s;
}

@keyframes wordDisplay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

main {
  background-color: #f4f4f4;
  padding: 10px;
  position: relative;
}

main section {
  margin-top: 50px;
}

main h1.leftSide {
  font-size: 48px;
  position: relative;
  top: 70px;
  left: 50px;
  z-index: 1;
}

main h1.leftSideOutline {
  font-size: 72px;
  position: relative;
  top: 30px;
  left: 90px;
  color: #f4f4f4;
  z-index: 0;
}

main h1.rightSide {
  text-align: right;
  font-size: 48px;
  position: relative;
  top: 70px;
  left: -50px;
  z-index: 1;
}

main h1.rightSideOutline {
  text-align: right;
  font-size: 72px;
  position: relative;
  top: 30px;
  left: -90px;
  color: #f4f4f4;
  z-index: 0;
}

main h1.line1 {
  text-shadow: -1px 0 #f37121, 0 1px #f37121, 1px 0 #f37121, 0 -1px #f37121;
}

main h1.line2 {
  text-shadow: -1px 0 #16817a, 0 1px #16817a, 1px 0 #16817a, 0 -1px #16817a;
}

main h1.line3 {
  text-shadow: -1px 0 #f19292, 0 1px #f19292, 1px 0 #f19292, 0 -1px #f19292;
}

main h1.line4 {
  text-shadow: -1px 0 #035aa6, 0 1px #035aa6, 1px 0 #035aa6, 0 -1px #035aa6;;
}

main div {
  z-index: 2;
}

section {
  padding: 0 50px;
}

button.MuiFab-primary {
  background-color: #85a392;
}

button.MuiFab-primary:hover {
  background-color: #85a392;
}

div.MuiSpeedDial-root {
  align-items: flex-end;
}

button.speed-dial-icon-button {
  position: relative;
  top: -3px;
}